
@import '../../common/colors.scss';
@import '../../common/functions.scss';

.cmp-wizard-form {
  .cmp-text {
    font-size: 1rem;
    color: #000c21;
    letter-spacing: 0;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .cmp-text-component-container .cmp-text > p {
    margin: 0;
  }

  .cmp-text.cmp-text--gray {
    color: #555555;
  }

  .cmp-text.cmp-text--bold {
    font-size: 0.875rem;
    font-weight: 600;
    line-height: 19px;
  }

  .cmp-text ul {
    list-style: none;
    padding-left: 2.5%;
  }


  .cmp-text ul li:before {
    content: '•';
    font-size: 1.25rem;
    padding-right: 20px;
    line-height: 22px;
  }
}

.apac-form {
  font-family: 'Arial';

  * {
    font-family: 'Arial' !important;
  }

  .cmp-wizard-form__body .cmp-wizard-form__panel a {
    color: $teal;
  }

  .cmp-wizard-form-container {
    background-color: $neutral-gray-1;

    .cmp-wizard-form__header {
        padding: 1rem 0;
        background-color: $neutral-gray-1;
        margin: 0;
    }

    .cmp-wizard-form {
      max-width: 810px;
    }

    .cmp-wizard-form__header .wizard-form-stepper span.MuiStepLabel-iconContainer.MuiStepLabel-alternativeLabel svg circle {
      fill: #00B1E2;
    }

    .cmp-wizard-form__header .wizard-form-stepper .Mui-disabled circle {
      stroke: #D9D8D7;
    }

    .cmp-wizard-form__header .wizard-form-stepper .Mui-active circle {
      stroke: #00B1E2;
    }

    .cmp-wizard-form__panel.active >div .MuiFormControl-root.cmp-textfield:nth-child(12) {
      margin-bottom: 0;
    }

    .cmp-wizard-form .MuiFormControl-root {
      margin-top: 0;
      padding-bottom: 18px;
      input {
        min-height: 23px;
      }
      &.cmp-textfield {
        padding-bottom: 18px;
        padding-top: 4px;
        margin-bottom: 10px;
        min-height: 52px;
        box-sizing: content-box;
        .MuiInputLabel-root {
          margin-top: -9px;
        }
      }
      &.cmp-dropdown {
          min-height: 48px;
          padding-top: 8px;
          box-sizing: content-box;
          .MuiInputLabel-root {
            margin-top: -5px;
          }
      }
    }

    .cmp-wizard-form__body .cmp-wizard-form__panel:first-child {
      .MuiFormControl-root {
        &.cmp-dropdown {
          margin-bottom: 10px;
        }
      }
    }

    .cmp-wizard-form > .MuiFormControl-root {
      background-color: white;
      padding-bottom: 60px;
    }

    .cmp-title.cmp-heading-h3 {
        margin-top: 32px;
        font-size: 16px;
        line-height: 21px;
        font-weight: 700;
        color: #262626;
    }

    .cmp-wizard-form__body > .cmp-wizard-form__panel.active > div:first-child {
      gap: 10px;
    }

    .cmp-wizard-form__panel.active .MuiFormControl-root label + .MuiInput-root {
      margin-top: 0;
    }

    .MuiFormControlLabel-root {
      align-items: flex-start;
      margin-bottom: 10px;
      margin-top: 10px;
    }

    .cmp-dropdown .MuiSvgIcon-root, .MuiAccordionSummary-expandIconWrapper .MuiSvgIcon-root {
        background-image: url('data:image/png;base64,');
        background-size: contain;
        width: 16px;
        background-repeat: no-repeat;
        background-position-y: center;

        path {
          display: none;
        }
    }

  }

  .cmp-wizard-form {
    .cmp-text {
      font-size: 16px;
      line-height: 18.4px;
      color: #262626;
      letter-spacing: 0;
      margin: 0;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
    }

    .cmp-text-component-container .cmp-text > p {
      margin: 0;
      font-size: 16px;
      line-height: 24px;
      color: #262626;
      margin-bottom: 22px;
    }

    .margin-bottom-0 {
      margin-bottom: 0 !important;
      p {
        margin-bottom: 0 !important;
      }
    }

    .margin-bottom-20 {
      p {
        margin-bottom: 20px !important;
      }
    }

    .margin-top-0 {
      margin-top: 0 !important;
    }

    .margin-top-20 {
      margin-top: 20px !important;
    }

    .margin-bottom-10 {
      margin-bottom: 10px !important;
    }

    .padding-bottom-0 {
      p {
        padding-bottom: 0px !important;
      }
    }

    .margin-top-40 {
      margin-top: 40px !important;
    }

    .padding-bottom-10 {
      p {
        padding-bottom: 10px !important;
      }
    }

    .margin-top-20 {
      margin-top: 20px !important;
    }

    .MuiCheckbox-root {
      padding-top: 2px;
      .MuiSvgIcon-root {
        color: $deep-teal;
      }
    }

    .MuiStack-root {
      align-items: flex-start;
      .MuiTypography-body1 {
        order: 2;
      }

      .MuiFormControlLabel-asterisk {
        line-height: 10px;
      }
    }

    .cmp-wizard-form__body {
      box-shadow: none;
      padding-bottom: 30px;

      @media (min-width: 900px) {
            padding: 60px 160px;
      }

      .cmp-checkbox {
        .MuiTypography-root {
          display: flex;
          flex-direction: row-reverse;
          gap: 4px;
        }
      }
    }

    .cmp-wizard-form__panel.active > div > div:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
      label {
        margin-bottom: 0;
      }
    }

    .cmp-checkbox-group {
      font-size: 16px;
      line-height: 24px;
      color: #262626;

      a {
        color: $teal !important;
        text-decoration: none;
      }
    }

    .cmp-wizard-form__panel {
      width: 400px;
      margin: 0 auto;
    }

    .wizard-form-stepper .MuiStepLabel-labelContainer {
      .MuiStepLabel-label {
        font-size: 16px;
        line-height: 24px;
        font-family: 'Arial';
        margin-top: 5px;
        color: #888B8D;
        &.Mui-active {
          color: #262626;
          font-weight: 700;
        }
      }
    }

    .cmp-bottom-button {
      width: 400px;
      margin: 0 auto;
      border: none;
      box-shadow: none;
      border-radius: 0;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      padding: 12px 20px;
      background-color: $teal;
      &:hover {
        background-color: tint($teal, 20%);
      }
      &:active, .MuiTouchRipple-rippleVisible {
        background-color: shade($teal, 0.2) !important;
      }
      &.cmp-bottom-button__invalid-inputs, &:disabled {
          .cmp-bottom-button__progress-indicator {
            color: #888B8D;
          }
          background-color: #E4E5E6 !important;
          color: #888B8D !important;
          font-size: 16px !important;
          line-height: 24px;
          font-family: 'Arial';
          font-weight: 400;
        }
    }

    .cmp-text.cmp-text--gray {
      color: #765000;
    }

    #introduction-text-4,
    #company-information-text-2,
    #company-information-text-3,
    #Contact-information-text-2,
    #Contact-information-text-3,
    #Contact-information-text-4,
    #Contact-information-text-5,
    #Address-information-text-2,
    #Address-information-title-4,
    #corporate-information-text-2,
    #corporate-information-text-3,
    #corporate-information-text-4 ,
    #declaration-text-2{
      font-size: 16px;
      line-height: 18.4px;
      font-weight: 400;
      color: #727679;
      p {
        font-size: 16px;
        line-height: 18.4px;
        font-weight: 400;
        color: #727679;
        padding-bottom: 20px;
        margin: 0;
      }
    }

    .cmp-text.cmp-text--bold {
      font-size: 0.875rem;
      font-weight: normal;
      line-height: 19px;
    }

    .cmp-text ul {
      list-style: none;
      padding-left: 2.5%;
    }


    .cmp-text ul li:before {
      content: '•';
      font-size: 1.25rem;
      padding-right: 20px;
      line-height: 22px;
    }

    .Mui-error {
      font-size: 12px;
      line-height: 16px;
      color: #CD163F;
      margin-top: 0;
    }

    .Mui-required {
      margin-top: 0;
    }


    .cmp-wizard-form__back-nav {
      display: none;
    }

    .cmp-upload .MuiBox-root .MuiTypography-caption {
        color: #262626;
        font-size: 16px;
        line-height: 24px;
        font-weight: 400;
    }

    .cmp-upload svg[data-testid='CloudUploadIcon'] {
      background-image: url(data:image/png;base64,);
      background-repeat: no-repeat;
      width: 24px;
      height: 24px;
      background-size: contain;
      path {
        display: none;
      }
    }

    .cmp-wizard-form__header .wizard-form-stepper span.MuiStepLabel-iconContainer.MuiStepLabel-alternativeLabel svg text {
      color: #888B8D;
    }

    .cmp-wizard-form__header .cmp-title.cmp-heading-h2 {
      font-size: 28px;
      line-height: 42px;
      color: $teal;
      font-family: 'Arial';
      font-weight: 400;
    }

    .Mui-focused {
      &.MuiInput-root.MuiInput-underline {
        &:after, &:not(.Mui-disabled):after, &:before, &:hover, &:hover:after, &:hover:not(.Mui-disabled):before {
          border-bottom-color: $teal;
        }
      }
    }

    .MuiInput-root.MuiInput-underline:hover:not(.Mui-disabled):before {
      border-bottom-color: $teal;
    }

    .MuiInputLabel-root {
      display: flex;
      flex-direction: row-reverse;
      font-size: 16px;
      line-height: 24px;
      justify-content: flex-end;
      margin-top: -14px;
      color: #888B8D;

      &.Mui-error {
        color: #CD163F;
        font-size: 16px;
        line-height: 16px;
        align-items: center;
      }

      .Mui-error:after {
        border-bottom-color: #CD163F;
      }
    }

    .cmp-upload > .MuiBox-root {
        margin-bottom: 0;
        background-color: #F8F8F8;

        &:hover {
          background-color: rgba(0, 111, 186, 0.1) !important;
          border-color: rgb(0, 111, 186);
        }

        .MuiButton-root {
          color: $teal;
          background-color: tint($teal, 90%);

          &:hover {
            background-color: tint($teal, 80%);
          }

          &:active, .MuiTouchRipple-rippleVisible {
            background-color: tint($teal, 70%);
          }
        }
    }

    .cmp-wizard-form__result-panel {
      height: auto;
      align-items: flex-start;
        .cmp-text-component-container {
          span, p {
            font-size: 16px !important;
            font-weight: 400;
            font-family: 'Arial';
            line-height: 24px;
            color: #262626;
          }
        }
        #submission-title-1 {
          span, p, strong {
            font-weight: 700 !important;
            font-family: 'Arial';
            font-size: 20px !important;
            line-height: 26px;
            color: #262626;
          }
        }

        .cmp-wizard-form__result-icon {
          margin-top: 5px;
          img {
            width: 32px;
            height: 32px;
          }
        }

        .cmp-wizard-form__try-again-button {
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          color: #00558C;
          background-color: transparent;
          border: 0;
          box-shadow: none;
          padding: 0;
        }
    }

    .cmp-wizard-form-container .cmp-wizard-form__result-icon img {
      width: 32px;
    }

    .cmp-title.cmp-heading-h2 {
      font-size: 20px;
      line-height: 26px;
      font-weight: 700;
      color: #262626;
    }
  }
}

// AEM overrides
[data-component="TdsForm"] ul {
  padding-left: 2.5%;
  & li {
    margin: 0;
    &:before {
      color: #000c21;
      width: auto;
    }
  }
}