@import '../../theme/variables';
.cmp-wizard-form {
  .cmp-checkbox-group {
    a {
      color: $link-color !important;
    }
  }
  .cmp-checkbox {
    .MuiFormHelperText-contained {
      margin-left: unset;
    }
