$color-cobalt: #006FBA;
$color-cherry: #CD163F;
$color-cool-gray-8: #888B8D;

.cmp-wizard-form {
  .cmp-multifield-component {
    .MuiAccordion-root {
      box-shadow: none;
      margin: 0 0;

      &.Mui-expanded .MuiButtonBase-root.Mui-expanded {
        min-height: unset;
      }

      &::before {
        display: none;
      }

      &.Mui-disabled {
        background-color: transparent;
      }
    }

    .MuiAccordionDetails-root {
      padding: 0;
    }

    .multifield-panel-header {
      padding: 0;

      .MuiAccordionSummary-content, .multifield-panel-header-title, .multifield-panel-header-status {
        display: flex;
        align-items: center;
        gap: 6px;
      }

      &.multifield-panel-header-clean {
        .MuiAccordionSummary-expandIconWrapper {
          display: none;
        }
      }

      &.multifield-panel-header-dirty {
        .MuiAccordionSummary-content {
          .MuiTypography-root.multifield-panel-header-status {
            margin-right: 8px;
          }
        }
      }

      .MuiAccordionSummary-content {
        margin: 14px 0;

        .MuiTypography-root .multifield-panel-header-title-text {
          font-family: Arial;
          color: $color-cool-gray-8;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 24px;
        }

        .multifield-panel-header-add {
          fill: $color-cool-gray-8;
        }

        &:hover {
          .MuiTypography-root .multifield-panel-header-title-text {
            color: $color-cobalt;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 24px;
          }

          .multifield-panel-header-add{
            fill: $color-cobalt;
          }
        }

        .MuiTypography-root.multifield-panel-header-status {
          margin-left: auto;
          justify-content: end;
          color: $color-cherry;
          margin-right: 0;

          .multifield-panel-header-warning {
            margin-left: -5px;
          }

          .multifield-panel-header-warning-text {
            white-space: nowrap;
            font-family: Arial;
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: 16px;
          }
        }
      }

      &.multifield-panel-header-dirty {
        background-color: transparent;
        border-bottom: none;
      }
    }
  }
}
