@import '../../theme/variables';

.cmp-textfield-autocomplete-magnifying-glass {
  .MuiInput-root {
    &.MuiInputBase-root {
      &.MuiAutocomplete-inputRoot {
        padding-right: 0;
      }
    }
  }
  .MuiAutocomplete-inputRoot {
    button.MuiButtonBase-root {
      order: 3;
    }
    .MuiAutocomplete-endAdornment {
      position: relative;
    }
  }
  .cmp-autocomplete__button-section {
    position: absolute;
    right: 0px;
    margin-bottom: 15px;

    .cmp-autocomplete__spinner {
      position: relative;
      top: 6px;
      right: 15px;
    }

    .cmp-button__autocomplete-search {
      background-color: #1d3152;
      border-radius: 2px;
      padding-right: 10px;
      text-transform: unset;
      box-shadow: unset;
      &:hover {
        background-color: #1d3152;
      }
      &:disabled {
        background-color: #e4e5e6;
        color: #888b8d;
      }
      svg {
        width: 20px;
        margin-top: 2px;
        margin-left: 3px;
        height: 20px;
      }
    }
  }
}

.cmp-autocomplete {
  &__option-name {
    color: $autocomplete-company-color;
  }
  &__option-additional-info {
    color: $autocomplete-additional-info-color;
  }
}
